/* 
* 
*@,,Number.console()
*@,,this,param.console()
*@:
*Number.console([1,2,3]);
*[1,2,3].console('');
*/
Number.console = function(res) {
  console.log(res);
};
Number.prototype.console = function(res) {
  console.log(this);
  console.log(res);
};



