/**
 * AES
 * @key,,
 * aesEncrypt('123456') 
 * => 'Y+ahQX0nTjpYJeSHV00P0w=='
 * aesDecrypt('Y+ahQX0nTjpYJeSHV00P0w==') 
 * => '123456'
 */
import GibberishAES from './gibberish-aes';
// const key  = '20180514';// 
const key = [105, 97, 109, 103, 97, 121, 33, 33]; // 
const stringKey = key => key.map(v => String.fromCharCode(v)).join(''); // ASCII => iamgay!!

/**
 * trueKey
 */
var trueKey;
if (typeof key === 'string') {
  trueKey = key; // aesKey
} else {
  trueKey = stringKey(key); // aesKey
}

// aes
const aesEncrypt = item => GibberishAES.aesEncrypt(item, trueKey);
// aes
const aesDecrypt = item => GibberishAES.aesDecrypt(item, trueKey);

export {
	aesEncrypt,
	aesDecrypt
}


