/* ======== */
import {Message} from 'iview'

/**:
 * empty 
 * phone 
 * email 
 * password 
 * account 
 * greaterZero 
 */
const RegConfig = {
  empty: {
    validate: vData => {
      if (vData === undefined || vData === '' || vData.length === 0) {
        return false;
      } else {
        return true;
      }
    },
    message: ''
  },
  phone: {
    validate: vData => {
      return /^1[0-9]{10}$/.test(vData);
    },
    message: ''
  },
  email: {
    validate: vData => {
      return /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+$/.test(vData);
    },
    message: ''
  },
  account: { // 
    validate: vData => {
      return /^\w{3,15}$/.test(vData);
    },
    message: '315'
  },
  password: { // 
    validate: vData => {
      return /^\w{6,18}$/.test(vData);
    },
    message: '6-18'
  },
  greaterZero: {
    validate: vData => {
      return /^[1-9]\d*$/.test(vData);
    },
    message: ''
  }
};

/**
*
*params = [
  ['', '', 'empty'],
  ['123456', '', 'empty|password'],
]
**/
const RegFuntion = (params = []) => {
  for (let key1 = 0; key1 < params.length; key1++) {
    let _item = params[key1]; // 
    let _vCon = _item[0]; // 
    let _vErrTip = _item[1]; // 
    let _vRegArr = _item[2].split('|'); // ,
    for (let key2 = 0; key2 < _vRegArr.length; key2++) {
      let _vRegName = _vRegArr[key2]; // 
      let _vReturn = RegConfig[_vRegName].validate(_vCon);
      if (!_vReturn) {
        Message.warning(_vErrTip + RegConfig[_vRegName].message);
        return false;
      }
    }
  }
  return true;
};

/**
*,then
**/
const validate = (params = []) => {
  var RegResult = RegFuntion(params);
  return new Promise((resolve, reject) => {
    if(RegResult){
      resolve(true)
    }
  }).then(result =>{
    return result
  })
};

export {
  validate
}


