<template>
  <div class="pickerContent">
    <Row>
      <Col :span="24">
        <input v-model="value" type="text" ref="search" id="search" class="ivu-input" placeholder=""/>
      </Col>
    </Row>
    <div class="pickerMap" ref="map"></div>
  </div>
</template>

<script>
  /* eslint-disable */
  export default {
    name: 'AMapAddress',

    props: {
    },

    data() {
      return {
        value: '', // 
        longitude: '', // 
        latitude: '', // 
        address: '', // 
      }
    },
    watch: {
    },
    mounted() {
      this.loadMap();
    },
    methods: {
      // 
      loadMap() {
        const map = new AMap.Map(this.$refs.map, {
          zoom: 16,
          resizeEnable: true
        });
        this.map = map;
        const self = this;
        AMapUI.loadUI(['misc/PositionPicker'], function (PositionPicker) {
          // 
          var positionPicker = new PositionPicker({
            mode: 'dragMap',
            map: map
          });
          // 
          positionPicker.on('success', function (positionResult) {
            self.longitude = positionResult.position.lng;
            self.latitude = positionResult.position.lat;
            self.address = positionResult.address;
          });
          // 
          positionPicker.on('fail', function (positionResult) {
            self.longitude = 0;
            self.latitude = 0;
            self.address = '';
          });
          // 
          positionPicker.start();
        });

        // 
        var auto = new AMap.Autocomplete({
          input: 'search'
        });
        //
        AMap.event.addListener(auto, "select", function(e) {
          if (e.poi && e.poi.location) {
            map.setZoom(15);
            map.setCenter(e.poi.location);
          }
        });
      },
      // 
      getPosition() {
        return {
          value: this.value,
          longitude: this.longitude,
          latitude: this.latitude,
          address: this.address,
        }
      },
      // 
      setPosition(params={}) {
        this.value = params.value;
        this.longitude = params.longitude;
        this.latitude = params.latitude;
        this.address = params.address;
        var center = [Number(params.longitude), Number(params.latitude)];
        this.map.setZoomAndCenter(14, center);
      },
    },
  }
</script>

<style lang="less" scoped>
  .pickerContent{
    width: 100%;
  }
  .pickerMap{
    width: 100%;
    height: 200px;
    margin-top: 10px;
  }
</style>



