/*,,*/
import Vue from 'vue'

var mixin = {
  methods: {
    //
    handleRouter(name) {
      this.$router.push({ name });
    },
    //
    handleBack() {
      this.$router.go(-1)
    },
    //
    handleSearch() {
      this.params = Object.assign({}, this.params);
    },
    //,
    handleRemove(url, params = {}) {
      this.$Modal.confirm({
        title: '',
        content: ' ?',
        onOk: () => {
          this.$post(url, params).then(res => {
            this.$Message.success('');
            this.handleSearch();
          })
        },
        onCancel: () => {

        }
      });
    },
    formatDate() {
      console.log('---mixin---', '')
    },
  }
}

Vue.mixin(mixin)



