import Vue from 'vue'
import Router from 'vue-router'
//src@
import Main from '@/pages/Main'

Vue.use(Router)

//,Main
export const baseRouter = [{
    path: '/',
    name: 'login',
    title: '',
    component: resolve => require(['../pages/login/login.vue'], resolve)
  },
  {
    path: '/forget',
    name: 'forget',
    title: '',
    component: (resolve) => require(['../pages/login/forget.vue'], resolve)
  }
];

/*
 *Main,
 *@path 
 *@name 
 *@icon 
 *@title 
 *@toName 
 *@component 
 *@hasChildren 
 *@children 
 */
export const pagesRouter = [
  // {
  //   path: '/home',
  //   name: 'home',
  //   icon: 'home',
  //   title: '',
  //   toName: 'home-index',
  //   component: Main,
  //   hasChildren: false,
  //   children: [
  //     {
  //       path: 'index',
  //       name: 'home-index',
  //       title: '',
  //       component: resolve => require(['../pages/home3/index3'], resolve)
  //     }
  //   ]
  // },
  {
    path: '/device',
    name: 'device',
    icon: 'shangpin',
    title: '',
    toName: 'device-list',
    component: Main,
    hasChildren: false,
    children: [{
      path: 'list',
      name: 'device-list',
      title: '',
      component: resolve => require(['../pages/1_device/list'], resolve)
    }]
  },
  {
    path: '/account',
    name: 'account',
    icon: 'zhanghao',
    title: '',
    toName: 'account-list',
    component: Main,
    hasChildren: true,
    children: [{
        path: 'list',
        name: 'account-list',
        title: '',
        component: resolve => require(['../pages/2_account/list'], resolve)
      },
      {
        path: 'list2',
        name: 'account-list2',
        title: '',
        component: resolve => require(['../pages/2_account/list2'], resolve)
      },
      {
        path: 'list3',
        name: 'account-list3',
        title: '',
        component: resolve => require(['../pages/2_account/list3'], resolve)
      }
    ]
  },
  {
    path: '/xiaoer',
    name: 'xiaoer',
    icon: 'xiaoer',
    title: '',
    toName: 'xiaoer-list',
    component: Main,
    hasChildren: false,
    children: [{
      path: 'list',
      name: 'xiaoer-list',
      title: '',
      component: resolve => require(['../pages/3_xiaoer/list'], resolve)
    }]
  },
  {
    path: '/finance',
    name: 'finance',
    icon: 'caiwu',
    title: '',
    toName: 'finance-list',
    component: Main,
    hasChildren: true,
    children: [{
        path: 'list',
        name: 'finance-list',
        title: '',
        component: resolve => require(['../pages/4_finance/list'], resolve)
      },
      {
        path: 'list2',
        name: 'finance-list2',
        title: '',
        component: resolve => require(['../pages/4_finance/list2'], resolve)
      },
    ]
  },
  {
    path: '/flow',
    name: 'flow',
    icon: 'liucheng',
    title: '',
    toName: 'flow-list',
    component: Main,
    hasChildren: true,
    children: [{
        path: 'list',
        name: 'flow-list',
        title: '',
        component: resolve => require(['../pages/5_flow/list'], resolve)
      },
      {
        path: 'list2',
        name: 'flow-list2',
        title: '',
        component: resolve => require(['../pages/5_flow/list2'], resolve)
      },
      {
        path: 'list3',
        name: 'flow-list3',
        title: '',
        component: resolve => require(['../pages/5_flow/list3'], resolve)
      }
    ]
  },
  {
    path: '/shop',
    name: 'shop',
    icon: 'jinxiaocun',
    title: '',
    toName: 'shop-list',
    component: Main,
    hasChildren: true,
    children: [{
        path: 'list',
        name: 'shop-list',
        title: '',
        component: resolve => require(['../pages/6_shop/list'], resolve)
      },
      {
        path: 'list2',
        name: 'shop-list2',
        title: '',
        component: resolve => require(['../pages/6_shop/list2'], resolve)
      },
    ]
  },
  {
    path: '/message',
    name: 'message',
    icon: 'message',
    title: '',
    toName: 'message-list',
    component: Main,
    hasChildren: false,
    children: [{
      path: 'list',
      name: 'message-list',
      title: '',
      component: resolve => require(['../pages/7_message/list'], resolve)
    }]
  },
  {
    path: '/system',
    name: 'system',
    icon: 'xitong',
    title: '',
    toName: 'system-list',
    component: Main,
    hasChildren: true,
    children: [{
        path: 'list',
        name: 'system-list',
        title: '',
        component: resolve => require(['../pages/8_system/1_stock'], resolve)
      },
      {
        path: 'account_log',
        name: 'account_log',
        title: '',
        component: resolve => require(['../pages/8_system/account_log'], resolve)
      },
      {
        path: 'allocate_log',
        name: 'allocate_log',
        title: '',
        component: resolve => require(['../pages/8_system/allocate_log'], resolve)
      },
      {
        path: 'password',
        name: 'password',
        title: '',
        component: resolve => require(['../pages/8_system/password'], resolve)
      },
      {
        path: 'sanction',
        name: 'sanction',
        title: '',
        component: resolve => require(['../pages/8_system/sanction'], resolve)
      },
      {
        path: 'setting',
        name: 'setting',
        title: 'APP',
        component: resolve => require(['../pages/8_system/setting'], resolve)
      }
    ]
  },
];

/*
 *Main,
 *@path 
 *@name 
 *@title 
 *@component 
 */
export const othersRouter = [{
    path: '/user',
    name: 'user',
    title: '',
    component: Main,
    children: [{
        path: 'index',
        name: 'user-index',
        title: '',
        component: resolve => require(['../pages/user/index'], resolve)
      },
      {
        path: 'index2',
        name: 'user-index2',
        title: '',
        component: resolve => require(['../pages/user/index2'], resolve)
      },
    ]
  },
  {
    path: '/others',
    name: 'othersRouter',
    redirect: '/home',
    title: '',
    component: Main,
    children: [{
      path: 'edit',
      name: 'home-edit',
      title: '',
      component: resolve => require(['../pages/home/index'], resolve)
    }]
  },
];

//403,404,500
export const errRouter = [{
    path: '/*',
    name: 'error-404',
    title: '404-',
    component: resolve => require(['../pages/errorPage/404.vue'], resolve)
  },
  {
    path: '/403',
    name: 'error-403',
    title: '403-',
    component: resolve => require(['../pages/errorPage/403.vue'], resolve)
  },
  {
    path: '/500',
    name: 'error-500',
    title: '500-',
    component: resolve => require(['../pages/errorPage/500.vue'], resolve)
  },
];

//...,router
export default new Router({
  routes: [
    ...baseRouter,
    ...pagesRouter,
    ...othersRouter,
    ...errRouter,
  ]
})



