/* 
* 
*@,,Array.console()
*@,,this,param.console()
*@:
*Array.console([1,2,3]);
*[1,2,3].console('');
*/
Array.console = function(res) {
  console.log(res);
};
Array.prototype.console = function(res) {
  console.log(this);
  console.log(res);
};



