/* 
* 
*@,,String.console()
*@,,this,param.console()
*@:
*String.console([1,2,3]);
*[1,2,3].console('');
*/
String.console = function(res) {
  console.log(res);
};
String.prototype.console = function(res) {
  console.log(this);
  console.log(res);
};

