/* 
*@bind:
*@inserted ()
*@update VNode  VNode 
*@componentUpdated VNode  VNode 
*@unbind
* ( elbindingvnode  oldVnode)
*/
export default {
  bind: function (el) {
    console.log('---directive---','bind')
  },
  inserted: function (el) {
    console.log('---directive---','inserted')
  },
  update: function (el) {
    console.log('---directive---','update')
  },
  componentUpdated: function (el) {
    console.log('---directive---','componentUpdated')
  },
  unbind: function (el) {
    console.log('---directive---','unbind')
  }
}



