/*
 *
 */
import Vue from "vue"
import axios from "axios"
import { Notice, Message } from 'iview'
import qs from 'qs'
// ,,
// import './mock.js'
// URL
const baseUrl = process.env.NODE_ENV == "production" ? "/index.php/admin" : "http://120.24.55.58:8146/index.php/admin";
var http = axios.create({
  baseURL: baseUrl,
  headers: {
    'Content-Type': 'application/x-www-form-urlencoded'
  },
  withCredentials: true
});

//
(function() {
  //
  http.interceptors.request.use(function(config) {
    //
    // 
    // config.headers.zwtUserId='49b0559c-a383-460f-8428-b9522d05ee41';
    // 
    // config.data={
    //   zwtMenuId:'4cf6cafe8aa34e70b17379e27530a7c2',
    //   ...config.data
    // };
    //QSForm Data,
    config.data = qs.stringify(config.data);
    return config;
  }, function(err) {
    Notice.error({
      title: '',
      desc: err
    });
    return err;
  });

  //
  http.interceptors.response.use(function(response) {
    //
    const { status, statusText, data } = response;
    console.log(data)
      // 
    if (status != 200) {
      Notice.error({
        title: '',
        desc: statusText
      });
    }
    // 
    if (data.errcode != 0) {
      Message.destroy()
      Message.warning({
        content: data.errmsg
      });
    }
    // 
    if (data.errcode == '40000') {
      window.location.href = '/'
    }
    return data;
  }, function(err) {
    Notice.error({
      title: '',
      desc: err
    });
    return err;
  });

  //axios
  Vue.prototype.$http = http;

  //POST,
  Vue.prototype.$post = (url, params = {}) => {
    return new Promise((resolve, reject) => {
      http.post(url, params).then(response => {
        if (response.errcode == '0') {
          if (/add/.test(url) || /edit/.test(url)) {
            Message.success('')
          }
          resolve(response)
        }
      }).catch((error) => {
        reject(error)
      })
    })
  };
})()



